.footer {
    width: 100%;
    height: 200px;
    background: var(--navcolor);
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding-top: 10px;
  }
  
  .socialMedia svg {
    color: var(--white);
    margin: 20px;
    font-size: 70px;
    cursor: pointer;
    transition: color 100ms ease;
  }
  .socialMedia svg:hover{
    color: var(--textcolor)
  }

  .footer span {
    color: var(--white);
    font-size: 25px;
  }
  
  @media only screen and (max-width: 600px) {
    .footer svg {
      font-size: 50px;
    }
  }